const app = getApp();
import setemail from "@/components/setcenter/setemail.vue"
import setphone from "@/components/setcenter/setphone.vue"
export default{
	data(){
		return {
			tokey:"",
			statusBar:0,
		}
	},
	components:{
		setemail:setemail,
		setphone:setphone
	},
	onLoad(){
		this.statusBar = app.globalData.statusBar
	},
	created(){
		const _this = this
		uni.getStorage({
			key:"bindtokey",
			success(res){
				_this.tokey = res.data
				//res.data获取到用户的tokey
				uni.request({
					url:"http://hbk.huiboke.com/api/user/getUserBindInfo",
					method:"POST",
					data:{
						token:_this.tokey
					},
					success(resinfophoneAndemail){
						console.log(resinfophoneAndemail)
					}
				})
			}
		})
	}
}
